// src/app/finalizar/page.js
'use client';

import { useCart } from '../../context/CartContext';

export default function FinalizarPage() {
  const { cartItems, clearCart } = useCart();

  const totalQuantidade = cartItems.length;
  const totalValor = cartItems.reduce((sum, item) => sum + item.price, 0);

  const mensagem = encodeURIComponent(
    `Olá, gostaria de finalizar o pedido: ${cartItems
      .map((item) => item.name)
      .join(', ')} — Total: R$ ${totalValor.toFixed(2)}`
  );

  const whatsappLink = `https://wa.me/5500000000000?text=${mensagem}`;

  return (
    <div className="p-6">
      <h1 className="text-2xl font-semibold mb-4">Resumo do Pedido</h1>

      <ul className="mb-4">
        {cartItems.map((item, index) => (
          <li key={index} className="flex justify-between border-b py-2">
            <span>{item.name}</span>
            <span className="text-right">R$ {item.price.toFixed(2)}</span>
          </li>
        ))}
      </ul>

      <div className="text-lg font-medium mb-4">
        Total: {totalQuantidade} item(s) — R$ {totalValor.toFixed(2)}
      </div>

      <a
        href={whatsappLink}
        target="_blank"
        rel="noopener noreferrer"
        onClick={clearCart}
        className="inline-block bg-green-600 hover:bg-green-700 text-white py-2 px-4 rounded-lg"
      >
        Enviar no WhatsApp
      </a>
    </div>
  );
}

